
body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  color: #34495e;
  background-color: #edeef0;
}
.svg__home,
.svg__article {
  width: 20px;
  height: 20px;
}
.svg__code {
  width: 22px;
  height: 20px;
}
.l-mH--auto {
  margin: 0 auto;
}
.l-ta--c {
  text-align: center;
}
.l-flexH--sa {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.l-flexV--c {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.l-page--Full {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.l-pt10 {
  padding-top: 10px;
}
.l-pl10 {
  padding-left: 10px;
}
.u-link {
  text-decoration: none;
  color: currentColor;
}
.u-link--Active {
  color: #1abc9c;
}
.u-ul--Reset {
  list-style-type: none;
  padding-left: 0;
}
.u-borderBox {
  box-sizing: border-box;
}
.u-clearfix:after,
.u-clearfix:before {
  display: table;
  content: " ";
}
.u-clearfix:after {
  clear: both;
}
.u-boxShadow {
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.3);
}
.u-zIndex10 {
  z-index: 10;
}
.u-zIndex50 {
  z-index: 50;
}
button {
  border: none;
  outline: none;
  box-shadow: none;
  background: none;
  color: currentColor;
  padding: 0;
  cursor: pointer;
}
.b-menu {
  width: 25px;
  height: 25px;
  position: relative;
}
.b-menu:before,
.b-menu:after {
  content: ' ';
  position: absolute;
  background-color: #fff;
  display: block;
  width: 100%;
  height: 3px;
  left: 0;
  top: 11px;
  border-radius: 3px;
  transition: all 0.5s;
  transform-origin: 50% 50%;
}
.b-menu:before {
  transform: translateY(-6px);
}
.b-menu:after {
  box-shadow: 0 6px 0 0 #fff;
}
.b-menu--Active:before {
  transform: rotateZ(45deg) translateY(0);
}
.b-menu--Active:after {
  box-shadow: 0 0 0 0 #fff;
  transform: rotateZ(135deg) translateY(0);
}
.app__menu {
  background-color: #1abc9c;
}
.app__content {
  transition: transform 0.5s;
  box-shadow: 0 0 100px 5px rgba(0,0,0,0.3);
  background-color: #edeef0;
  transform-origin: 200px center;
}
.app__content--Active {
  transform: scale(0.9) translateX(200px);
}
.app__bodyer {
  background-color: #edeef0;
}


